<template>
  <div ref="mindMapContainer" class="c-mindmap-container"></div>
</template>

<script>
import MindMap from 'simple-mind-map'

export default {
  name: 'MindMap',
  props: {
    // 
    videoTitle: {
      type: String,
      default: ''
    },
    // 
    summaryData: {
      type: Array,
      default: () => []
    },
    // 
    config: {
      type: Object,
      default: () => ({
        layout: 'logicalStructure',
        theme: 'dark',
        nodeTextEditZIndex: 1000,
        nodeNoteTooltipZIndex: 1000
      })
    }
  },
  data() {
    return {
      mindMap: null
    }
  },
  mounted() {
    this.initMindMap()
  },
  watch: {
    summaryData: {
      handler() {
        if (this.mindMap) {
          this.updateMindMapData()
        }
      },
      deep: true
    },
    videoTitle() {
      if (this.mindMap) {
        this.updateMindMapData()
      }
    }
  },
  beforeDestroy() {
    this.destroyMindMap()
  },
  methods: {
    initMindMap() {
      if (!this.$refs.mindMapContainer) {
        console.error('')
        return
      }
      
      // 
      this.destroyMindMap()
      
      try {
        const mindMapData = this.generateMindMapData()
        
        // 
        if (!mindMapData || !mindMapData.data || !mindMapData.data.text) {
          throw new Error('')
        }
        
        // 
        const mindMapConfig = {
          el: this.$refs.mindMapContainer,
          data: mindMapData,
          layout: this.config.layout || 'logicalStructure',
          theme: this.config.theme || 'dark',
          nodeTextEditZIndex: this.config.nodeTextEditZIndex || 1000,
          nodeNoteTooltipZIndex: this.config.nodeNoteTooltipZIndex || 1000
        }
        
        console.log(':', mindMapConfig) // 
        
        this.mindMap = new MindMap(mindMapConfig)
        
        // 
        this.mindMap.on('node_click', (node, e) => {
          const nodeData = node.getData()
          
          // 
          this.$emit('node-click', nodeData)
          
          // 
          if (nodeData.nodeType === 'chapter' && nodeData.startTime) {
            this.$emit('time-click', nodeData.startTime)
          } else if (nodeData.nodeType === 'summary' && nodeData.parentStartTime) {
            this.$emit('time-click', nodeData.parentStartTime)
          }
        })
        
        // 
        this.$emit('initialized', this.mindMap)
        
      } catch (error) {
        console.error(':', error)
        this.$emit('error', error)
      }
    },
    
    destroyMindMap() {
      if (this.mindMap) {
        try {
          this.mindMap.destroy()
        } catch (error) {
          console.error(':', error)
        }
        this.mindMap = null
      }
    },
    
    updateMindMapData() {
      if (this.mindMap) {
        const data = this.generateMindMapData()
        this.mindMap.setData(data)
      }
    },
    
    generateMindMapData() {
      // 
      const rootNode = {
        data: {
          text: this.videoTitle || ''
        },
        children: []
      }
      
      // summaryData
      if (!this.summaryData || !Array.isArray(this.summaryData) || this.summaryData.length === 0) {
        console.warn('summaryData')
        return rootNode
      }
      
      // summary-by-vidlisttitle
      rootNode.children = this.summaryData
        .filter(item => item && typeof item === 'object') // 
        .map((item, index) => {
          const firstLevelNode = {
            data: {
              text: item.title || ` ${index + 1}`,
              startTime: item.startTime || '00:00:00',
              nodeType: 'chapter'
            },
            children: []
          }
          
          // listitemsummary
          if (item.summary) {
            if (Array.isArray(item.summary) && item.summary.length > 0) {
              firstLevelNode.children = item.summary
                .map((summaryItem, summaryIndex) => {
                  let text = ''
                  
                  if (typeof summaryItem === 'string') {
                    text = summaryItem.trim()
                  } else if (summaryItem && typeof summaryItem === 'object') {
                    text = summaryItem.text || summaryItem.content || summaryItem.summary || ` ${summaryIndex + 1}`
                  } else {
                    text = String(summaryItem || ` ${summaryIndex + 1}`)
                  }
                  
                  return {
                    data: {
                      text: text || ` ${summaryIndex + 1}`,
                      nodeType: 'summary',
                      parentStartTime: item.startTime || '00:00:00'
                    }
                  }
                })
                .filter(node => node.data.text && node.data.text.length > 0) // 
            } else if (typeof item.summary === 'string' && item.summary.trim()) {
              // summary
              firstLevelNode.children = [{
                data: {
                  text: item.summary.trim(),
                  nodeType: 'summary',
                  parentStartTime: item.startTime || '00:00:00'
                }
              }]
            }
          }
          
          return firstLevelNode
        })
        .filter(node => node && node.data && node.data.text) // 
      
      console.log(':', rootNode) // 
      return rootNode
    },
    
    // 
    getMindMapInstance() {
      return this.mindMap
    },
    
    exportData() {
      return this.mindMap ? this.mindMap.getData() : null
    },
    
    fitView() {
      if (this.mindMap) {
        this.mindMap.view.fit()
      }
    },
    
    reset() {
      if (this.mindMap) {
        this.mindMap.view.reset()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.c-mindmap-container {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  background-color: #0f0f0f;
  border: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;
}

// simple-mind-map
